//
// styles/_fonts.scss
//

@font-face {
	font-family: 'Fontello';
	font-style: normal;
	font-weight: normal;
	src: url('../fonts/Fontello.eot') format('embedded-opentype'),
		inline('../fonts/Fontello.woff') format('woff');
}

@font-face {
	font-family: 'Adobe Blank';
	font-style: normal;
	font-weight: normal;
	src: url('../fonts/AdobeBlank.eot') format('embedded-opentype'),
		inline('../fonts/AdobeBlank.woff') format('woff');
}

// Duplicate Pinyon Script $font-face due to IE8 not liking the data-uri used
// for the WOFF version.
@font-face {
	font-family: 'Pinyon Script';
	font-style: normal;
	font-weight: normal;
	src: url('../fonts/PinyonScript.eot');
}

@font-face {
	font-family: 'Pinyon Script';
	font-style: normal;
	font-weight: normal;
	src: url('../fonts/PinyonScript.eot') format('embedded-opentype'),
		inline('../fonts/PinyonScript.woff') format('woff');
}
