//
// styles/_portfolio.scss
//

@import 'reference/mixins';
@import 'reference/variables';

.portfolio {
	@include clear-fix;

	&__list {
		@include clean-list;

		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		margin: 0 auto;
	}

	&__item {
		box-sizing: border-box;
		flex: 0 0 100%;
		margin-bottom: 2rem;
		text-align: center;
		width: 100%;

		@include media-min(sm) {
			$width: percentage(1 / 2);

			flex: 0 0 $width;
			max-width: $width;
			padding: 0 1rem;
			width: $width;
		}

		@include media-min(md) {
			$width: percentage(1 / 3);

			flex: 0 0 $width;
			max-width: $width;
			width: $width;
		}
	}

	&__link {
		$box-shadow: 1px;

		background-color: $white;
		box-shadow: $box-shadow $box-shadow $box-shadow rgba($grey-darker, .1);
		box-sizing: border-box;
		display: inline-block;
		max-width: 100%;
		padding: 1rem;
		text-decoration: none;
		transition: transform .3s ease-in-out;
		width: 100%;

		&,
		&:visited {
			color: $grey-dark;
		}

		&:focus,
		&:hover {
			color: $blue;
			transform: scale(1.025);
		}
	}

	&__frame {
		$height: 180px;

		height: auto;
		margin-bottom: 10px;
		max-width: 100%;

		.lt-ie9 & {
			height: $height;
			max-width: none;
			width: 280px;
		}
	}

	&__image {
		height: auto;
		max-width: 100%;

		.no-js & {
			display: none;
		}

		.no-js noscript & {
			display: block;
		}
	}

	&__title {
		display: block;
	}

	&__desc {
		color: $grey;
		display: block;
		margin-top: 5px;
	}
}
