//
// styles/_socialmedia.scss
//

@import 'reference/mixins';
@import 'reference/variables';

.socialmedia {
	&__list {
		@include clean-list;

		text-align: center;
	}

	li {
		display: inline-block;
		margin-left: 40px;

		&:first-child {
			margin-left: 0;
		}
	}

	&__icon {
		display: block;
		font-family: $font-family-blank;
		font-size: $font-size-narrative;
		text-decoration: none;
		transition: color .3s ease-in-out, transform .3s ease-in-out;

		&,
		&:visited {
			color: $grey-dark;
		}

		&:hover,
		&:focus {
			color: $blue;
			transform: scale(1.05);
		}

		&::first-letter {
			font-family: $font-family-icon;
		}
	}
}
